body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  text-align: center;
}

.screen {
  padding: 20px;
}

.tag {
  display: block;
  margin-bottom: 10px;
  font-size: 1.2rem;
  cursor: pointer;
}

.tag input {
  width: 1rem;
  height: 1rem;
  margin-right: 5px;
}

.remove-button {
  cursor: pointer;
  margin-left: 10px;
}

.image-list {
  list-style: none;
  padding-left: 0;
}

.image-list li {
  display: flex;
  align-items: center;
}
